<template>
  <div
    v-if="user"
    class="DashboardUsersShow">

    <!-- ### START TOOLBAR ### -->
    <div class="toolbar">
      <div class="group">
        <div class="title">
          <router-link :to="{name: 'DashboardUsers'}">Пользователи</router-link>
          <div class="slash"><i class="el-icon-arrow-right" /></div>
          <div class="name truncate">{{ user.first_name }} {{ user.last_name }}</div>
        </div>
        <el-tag
          v-if="user.id === current_user.id"
          type="success"
          disable-transitions>Вы</el-tag>
        <el-tag
          v-if="user.admin"
          type="primary"
          disable-transitions>Админ</el-tag>
      </div>
      <div class="group">
        <el-button
          v-if="user.id !== current_user.id"
          size="small"
          type="danger"
          @click="deleteUser">Удалить</el-button>
      </div>
    </div>
    <!-- ### END TOOLBAR ### -->

    <!-- ### START CONTAINER ### -->
    <div class="container">
      <el-form
        ref="userForm"
        :model="user"
        :disabled="!current_user.admin"
        class="user-form">
        <div class="body">
          <h3 class="title">
            Карточка пользователя
            <span class="small">(UID {{ id }})</span>
          </h3>

          <el-form-item>
            <el-input
              v-model="user.first_name"
              placeholder="Имя"
              @change="updateUser" />
          </el-form-item>
          <el-form-item>
            <el-input
              v-model="user.last_name"
              placeholder="Фамилия"
              @change="updateUser" />
          </el-form-item>
          <el-form-item>
            <el-input
              v-model="user.second_name"
              placeholder="Отчество"
              @change="updateUser" />
          </el-form-item>
          <el-form-item>
            <el-input
              v-model="user.email"
              placeholder="Электронная почта"
              :readonly="true" />
          </el-form-item>
          <el-form-item label="Администратор">
            <el-switch
              v-model="user.admin"
              :disabled="true"/>
          </el-form-item>
          <div class="hint">
            <i class="fa fa-check fa-fw" />
            Изменения сохраняются автоматически
          </div>
        </div>
      </el-form>
    </div>
    <!-- ### END CONTAINER ### -->
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'DashboardUsersShow',

  props: {
    id: {
      type: [Number, String],
      default: 0,
    },
  },

  data() {
    return {
      user: null,
    };
  },

  computed: {
    ...mapState('auth', ['current_user']),
  },

  created() {
    this.getUser();
  },

  methods: {
    async getUser() {
      const response = await this.$http.get(`users/${this.id}`);
      this.user = response.data;
    },

    async updateUser() {
      this.$http.patch(`/users/${this.id}`, { user: this.user })
        .then(() => {
          this.$message.success('Аккаунт обновлен');
          this.getUser();
        });
    },

    async deleteUser() {
      try {
        await this.$confirm('Пользователь будет безвозвратно удален. Вы уверены?', 'Удалить аккаунт', {
          confirmButtonText: 'Удалить',
          cancelButtonText: 'Отменить',
          type: 'warning',
        });
        await this.$http.delete(`users/${this.id}`);
        this.$message.success('Вы удалили пользователя');
        this.$router.push({ name: 'DashboardUsers' });
      } catch (e) {
        this.$message.warning('Аккаунт не был удален.');
      }
    },
  },
};
</script>

