  .DashboardUsersShow {
    display: grid;
    grid-template-rows: 60px calc(100vh - 120px);
    height: 100%;

    .container {
      padding: 1rem;
      overflow: auto;
      background-color: $gray-silver;

      .user-form {
        background-color: #fff;
        border-radius: 8px;
        margin-bottom: 1rem;

        .title {
          margin-bottom: 2rem;
          font-weight: 400;
          .small {
            font-size: 0.75rem;
            color: $gray;
            margin-left: 0.5rem;
          }
        }

        .body {
          padding: 2rem;
          .hint {
            color: $success;
          }
        }
      }
    }
  }
</style>
